$size-watch: 600px;

body {
  margin: 0;
}

.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.watch {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: $size-watch;
  width: $size-watch;
  border-radius: 50%;
  box-shadow: inset 0 0 30px 10px rgba(8, 8, 8, 0.3);
}

.center {
  position: absolute;
  height: 5%;
  width: 5%;
  transform: translate(-50%, -50%);
  top: 50%;
  left: 50%;
  background-color: #4b9aaa;
  border-radius: 50%;
}

.minute-hand {
  position: absolute;
  width: calc(100%/60);
  height: calc(100%/3);
  background-color: #d15559;
  border-radius: 50px;
  transform: translate(-50%, 0);
  bottom: 50%;
  transform-origin: 50% 100%;
  animation: hands-rotate 3600s linear infinite;
}

.second-hand {
  position: absolute;
  width: calc(100%/60);
  height: 50%;
  background-color: #4b9aaa;
  border-radius: 50px;
  transform: translate(-50%, 0);
  bottom: 50%;
  transform-origin: 50% 100%;
  animation: hands-rotate 60s linear infinite;
}

@keyframes hands-rotate {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
